rowser-default-font-size = 10px /*变量的值可以根据自己需求定义*/\n\n// @function rem($px)\n//     @return $px / $browser-default-font-size * 1rem\npre\n    display: inline\n    width: 80ch /* classic terminal width for code sections */\n\nhtml, body\n    margin: 0\n    padding: 0\n    font-size: $browser-default-font-size /* 10 ÷ 16 × 100% = 62.5% */\n\nbody\n    font-family: 'Pingfang SC', 'Helvetica Neue', Helvetica, Arial, sans-serif\n    color: #fff\n    min-width: 230px\n    margin: 0 auto\n    -webkit-font-smoothing: antialiased\n    -moz-font-smoothing: antialiased\n    font-smoothing: antialiased\n    font-weight: 600\n    transition: .4s all linear\n\n// @mixin clearfix\n//     &:before,\n//     &:after\n//         content: \"\"\n//         display: table\n//     &:after\n//         clear: both\n//         overflow: hidden\n\n.clearfix\n    @include clearfix\n.row\n    width: 100%\n    padding: 0px 10px\n.btn\n    margin: 0\n    padding: 0\n    border: 0\n    font-size: 1.5rem\n    vertical-align: baseline\n    color: #fff\n    padding: 10px 15px\n    -webkit-font-smoothing: antialiased\n    -moz-font-smoothing: antialiased\n    font-smoothing: antialiased\n    @mixins appearance(none)\n    text-align: center\n.btn-primary\n    background-color: $primary-color\n.btn-link\n    color: $gray-color\n    background-color: #fff\n\na, a:visited, a:hover, a:active\n    text-decoration: none;\n    color: inherit;\n\nbutton,\ninput[type=\"checkbox\"]\n    outline: none\ninput[type=\"text\"]\n    padding: 10px 15px\n    color: $gray-color\n    font-size: 1.5rem\n    border: 1px solid $gray-color\n    &:hover\n        border: 1px solid $primary-color\n    @include input-placeholder\n        color: $gray-color\n        font-style: italic\n        font-size: 1.2rem\n        text-align: center\n        letter-spacing: 1rem\n.hidden\n    display: none\n\n.pull-left\n    float: left\n.pull-right\n    float: right\n\nsection.body\n    // margin-top: rem(60px)\n    margin-top: 60px\n\n#app\n    position: absolute\n    width: 100%\n    top: 0\n    left: 0\n    right: 0\n    font-size: $browser-default-font-size\n","pre {\n  display: inline;\n  width: 80ch /* classic terminal width for code sections */;\n}\nhtml,\nbody {\n  margin: 0;\n  padding: 0;\n  font-size: 10px /*变量的值可以根据自己需求定义*/ /* 10 ÷ 16 × 100% = 62.5% */;\n}\nbody {\n  font-family: 'Pingfang SC', 'Helvetica Neue', Helvetica, Arial, sans-serif;\n  color: #fff;\n  min-width: 230px;\n  margin: 0 auto;\n  -webkit-font-smoothing: antialiased;\n  -moz-font-smoothing: antialiased;\n  font-smoothing: antialiased;\n  font-weight: 600;\n  transition: 0.4s all linear;\n}\n@include clearfix;\n.row {\n  width: 100%;\n  padding: 0px 10px;\n}\n.btn {\n  margin: 0;\n  padding: 0;\n  border: 0;\n  font-size: 1.5rem;\n  vertical-align: baseline;\n  color: #fff;\n  padding: 10px 15px;\n  -webkit-font-smoothing: antialiased;\n  -moz-font-smoothing: antialiased;\n  font-smoothing: antialiased;\n  text-align: center;\n}\n@mixins appearance(none);\n.btn-primary {\n  background-color: #7ed321;\n}\n.btn-link {\n  color: #a2a2a2;\n  background-color: #fff;\n}\na,\na:visited,\na:hover,\na:active {\n  text-decoration: none;\n  color: inherit;\n}\nbutton,\ninput[type=\"checkbox\"] {\n  outline: none;\n}\ninput[type=\"text\"] {\n  padding: 10px 15px;\n  color: #a2a2a2;\n  font-size: 1.5rem;\n  border: 1px solid #a2a2a2;\n}\ninput[type=\"text\"]:hover {\n  border: 1px solid #7ed321;\n}\n@include input-placeholder {\n  color: #a2a2a2;\n  font-style: italic;\n  font-size: 1.2rem;\n  text-align: center;\n  letter-spacing: 1rem;\n}\n.hidden {\n  display: none;\n}\n.pull-left {\n  float: left;\n}\n.pull-right {\n  float: right;\n}\nsection.body {\n  margin-top: 60px;\n}\n#app {\n  position: absolute;\n  width: 100%;\n  top: 0;\n  left: 0;\n  right: 0;\n  font-size: 10px /*变量的值可以根据自己需求定义*/;\n}\n.header {\n  position: relative;\n  width: 100%;\n  height: 50px;\n  line-height: 50px;\n  font-size: 24px;\n  padding-top: 15px;\n  text-align: center;\n  color: #fff;\n}\n.menus {\n  position: relative;\n  top: 20px;\n  width: 100%;\n  height: 60px;\n  margin-bottom: 40px;\n}\n.menus ul {\n  color: #fff;\n  font-size: 16px;\n  padding: 0;\n  font-weight: 200;\n  text-align: center;\n}\n.menus ul li {\n  display: inline-block;\n  list-style: none;\n  padding: 5px 20px 5px 10px;\n  opacity: 1;\n  cursor: pointer;\n  color: #fff;\n  margin: 0 5px;\n  border-bottom: 1px solid rgba(255,255,255,0);\n  transition: all 0.2s linear;\n}\n.menus ul li.tagged {\n  border-bottom: 1px solid #fff;\n}\n.menus ul li.tagged::after {\n  content: 'X';\n  position: relative;\n  top: 0;\n  right: -10px;\n  display: inline-block;\n  width: 1px;\n  height: 100%;\n  font-size: 14px;\n  color: #9e9e9e;\n}\n.menus ul li.tagged:hover::after {\n  color: #fff;\n}\n.separator {\n  position: relative;\n  height: 1px;\n  background-color: #ddd;\n  width: 25%;\n  margin: 20px auto 10px auto;\n  text-align: center;\n}\n.home-container {\n  position: relative;\n  width: 100%;\n  overflow: auto;\n  background-color: transparent;\n}\n.sd-logo-timestamp {\n  position: relative;\n  margin-left: 10%;\n  margin: 10% auto;\n  width: 80%;\n  text-align: center;\n  height: 150px;\n}\n.sd-logo-timestamp #sd-logo {\n  position: relative;\n  width: 150px;\n  height: 150px;\n  text-align: center;\n  display: inline-block;\n  transition: all 0.5s ease 0.5s;\n}\n.sd-logo-timestamp .time-text {\n  position: relative;\n  display: inline-block;\n  text-align: right;\n  margin-left: 80px;\n}\n.sd-logo-timestamp .time-text .time-local-text {\n  position: relative;\n  color: #fff;\n  font-size: 60px;\n  display: block;\n  letter-spacing: 2px;\n  text-align: right;\n  min-width: 250px;\n  font-weight: lighter;\n  font-family: PingFang;\n}\n.sd-logo-timestamp .time-text .time-color-text {\n  font-size: 15px;\n  color: #ddd;\n  display: block;\n  position: relative;\n  top: 0;\n  right: 0;\n  font-weight: lighter;\n  letter-spacing: 0;\n}\n.fade-transition {\n  transition: all 1s ease 0.5s;\n  opacity: 0.75;\n  overflow: hidden;\n}\n.fade-enter,\n.fade-leave {\n  opacity: 0;\n}\n.wipe-transition {\n  transition: all 0.5s ease 0.8s;\n  opacity: 1;\n  overflow: hidden;\n  margin-top: -5px;\n}\n.wipe-enter,\n.wipe-leave {\n  opacity: 0;\n  margin-top: 0;\n}\n.wipe-middle-transition {\n  transition: all 0.5s ease 0.5s;\n  opacity: 1;\n  overflow: hidden;\n  width: 70%;\n}\n.wipe-middle-enter,\n.wipe-middle-leave {\n  opacity: 0;\n  width: 0%;\n}\n.wipe-top-transition {\n  transition: all 0.5s ease 2.2s;\n  opacity: 1;\n  overflow: hidden;\n  margin-top: 0;\n}\n.wipe-top-enter,\n.wipe-top-leave {\n  opacity: 0;\n  margin-top: 10px;\n}\n","@import './common/common.styl'\n// @import './common/header.styl'\n\n// @import './mine/appList.styl'\n// @import './mine/profile.styl'\n\n// @import './user/activeWelcome.styl'\n// @import './user/login.styl'\n// @import './user/register.styl'\n// @import './user/resetPwd.styl'\n\n// @import './wedding/photo.styl'\n$sdWelcomeRowWidth = 150px\n\n.header\n    position: relative\n    width: 100%\n    height: 50px\n    line-height: 50px\n    font-size: 24px\n    padding-top: 15px\n    text-align: center\n    color: #fff\n\n.menus\n    position: relative\n    top: 20px\n    width: 100%\n    height: 60px\n    margin-bottom: 40px\n    ul\n        color: #fff\n        font-size: 16px\n        padding: 0\n        font-weight: 200\n        text-align: center\n        li\n            display: inline-block\n            list-style: none\n            padding: 5px 20px 5px 10px\n            opacity: 1\n            cursor: pointer\n            color: #fff\n            margin: 0 5px\n            border-bottom: 1px solid rgba(255, 255, 255, 0)\n            transition: all .2s linear\n            &.tagged\n                border-bottom: 1px solid #fff\n                &::after\n                    content: 'X'\n                    position: relative\n                    top: 0\n                    right: -10px\n                    display: inline-block\n                    width: 1px\n                    height: 100%\n                    font-size: 14px\n                    color: #9E9E9E\n                &:hover\n                    &::after\n                        color: #fff\n\n.separator\n    position: relative\n    height: 1px\n    background-color: #ddd\n    width: 25%\n    margin: 20px auto 10px auto\n    text-align: center\n\n.home-container\n    position: relative\n    width: 100%\n    overflow: auto\n    background-color: transparent\n\n.sd-logo-timestamp\n    position: relative\n    margin-left: 10%\n    margin: 10% auto\n    width: 80%\n    text-align: center\n    height: $sdWelcomeRowWidth\n    #sd-logo\n        position: relative\n        width: $sdWelcomeRowWidth\n        height: $sdWelcomeRowWidth\n        text-align: center\n        display: inline-block\n        transition: all .5s ease .5s\n    .time-text\n        position: relative\n        display: inline-block\n        text-align: right\n        margin-left: 80px\n        .time-local-text\n            position: relative\n            color: #fff\n            font-size: 60px\n            display: block\n            letter-spacing: 2px\n            text-align: right\n            min-width: 250px\n            font-weight: lighter\n            font-family: PingFang\n        .time-color-text\n            font-size: 15px\n            color: #ddd\n            display: block\n            position: relative\n            top: 0\n            right: 0\n            font-weight: lighter\n            letter-spacing: 0\n\n.fade-transition\n    transition: all 1s ease .5s\n    opacity: .75\n    overflow: hidden\n/* .expand-enter defines the starting state for entering */\n/* .expand-leave defines the ending state for leaving */\n.fade-enter, .fade-leave\n    opacity: 0\n\n.wipe-transition\n    transition: all .5s ease .8s\n    opacity: 1\n    overflow: hidden\n    margin-top: -5px\n.wipe-enter, .wipe-leave\n    opacity: 0\n    margin-top: 0\n\n.wipe-middle-transition\n    transition: all .5s ease .5s\n    opacity: 1\n    overflow: hidden\n    width: 70%\n.wipe-middle-enter, .wipe-middle-leave\n    opacity: 0\n    width: 0%\n\n.wipe-top-transition\n    transition: all .5s ease 2.2s\n    opacity: 1\n    overflow: hidden\n    margin-top: 0\n.wipe-top-enter, .wipe-top-leave\n    opacity: 0\n    margin-top: 10px\n\n\n"],"sourceRoot":"webpack://"}]);

